
import Vue from 'vue'
import { mapGetters } from 'vuex'

export default Vue.extend({
  name: 'Steps',
  props: {
    value: {
      type: Number,
      required: true,
    },
    length: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      oldIndex: 0,
    }
  },
  methods: {
    update(index: number) {
      this.oldIndex = this.value
      this.$emit('input', index)
    },
    finish() {
      this.$emit('finish')
    },
  },
  computed: {
    ...mapGetters(['Filters']),
    Completed(): boolean {
      return this.Filters.length == 4
    },
  },
})
